

























/* Panel Styles Styles */
.panel-title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  list-style-type: none;
  padding: 10px;
  height: 60px;
  width: 100%;
  background: -webkit-gradient(linear, left top, right top, from(rgba(52, 42, 107, 0.73)),to(rgba(80, 122, 167, 0.31)));
  background: linear-gradient(to right, rgba(52, 42, 107, 0.73) 0%,rgba(80, 122, 167, 0.31) 100%);
  margin-top: 20px;
}
.panel-title > h3 {
  text-align: left;
  margin-left: 25px;
  letter-spacing: 8px;
  font-size: 14px;
  color: #fff;
  font-weight: 400;
  margin-top: 16px;
}
.panel-title > h4 {
  text-align: right;
  margin-left: 25px;
  letter-spacing: 2px;
  font-size: 12px;
  color: #fff;
  font-weight: 400;
  margin-top: 16px;
}



